
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Badge, Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import Pagination from '../Base/Paginations/Pagnations';
import { getAllBedTypes } from '../Apis';

//import bedtypesData from './bedtypesData'
const addbedtype = 'bedtype/add'

function BedTypeRow(props) {
  const bedtypes = props.bedtypes
  const bedtypeLink = `/bedtypes/${bedtypes.id}`
  const updatebedtype = `bedtype/update/${bedtypes.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={bedtypes.id.toString()}>
      <th scope="row"><Link to={bedtypeLink}>{bedtypes.id}</Link></th>
      <td ><Link to={bedtypeLink}>{bedtypes.name}</Link></td>
      <td>{bedtypes.description}</td>
      {/* <td >{bedtypes.hospital_id}</td> */}
      <td ><Link to={updatebedtype}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

class BedTypes extends Component {

  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      totalPages: 0,
      totalRecords: 0
    };
  }

  componentDidMount() {
    this.getAllBedTypes(1)
  
  }
  getAllBedTypes=async(page)=>{
    const responseData= await getAllBedTypes(page);
    console.log(responseData,'response')
      if(responseData.status == 200){
        this.setState({
          
              isLoaded: true,
              items: responseData.data.result,
              totalPages: responseData.data.pages,
              totalRecords: responseData.data.count
            
        })
      } else if (responseData.status == 404) {
        console.warn(responseData.data.message);
      } 
  }
  onPageChanged = data => {
    //const { allCountries } = this.state;
   
    const { currentPage, totalPages, pageLimit } = data;
    this.getAllBedTypes(currentPage)
    console.log(data);
  };


  render() {

    const bedtypeList = this.state.items; //bedtypesData.filter((bedtype) => bedtype.id < 10)
    const Totrecords = parseInt(this.state.totalRecords); // total bedtypes records.
    let pagination;

    if(Totrecords !== 0 ){
      pagination = <Pagination totalRecords={Totrecords} pageLimit={5} pageNeighbours={1} onPageChanged={this.onPageChanged} />
    }

    return (
      <div className="animated fadeIn">
        <Row>
          <Col xl={2}>
          </Col>
          <Col xl={8}>
            <Card>
              <CardHeader>
                <i className="fa fa-align-justify"></i> bedtypes 
                <Link to={addbedtype}><button onClick={this.showHospitalInfo} className='btn btn-success  upActionbtn'>+ Add BedType</button></Link>
              </CardHeader>
              <CardBody>
                <Table responsive hover>
                  <thead>
                    <tr>
                      <th scope="col">Id</th>
                      <th scope="col">Name</th>
                      <th scope="col">Description</th>
                      {/* <th scope="col">Hospital Id</th> */}
                      <th scope="col">Edit BedType</th>
                    </tr>
                  </thead>
                  <tbody>
                    {bedtypeList.map((bedtypes, index) =>
                      <BedTypeRow key={index} bedtypes={bedtypes}/>
                    )}
                  </tbody>
                </Table>
              </CardBody>              
              {pagination}
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default BedTypes;
